.Resume{
    border-radius: 50px;
    background: #fcba03;
    white-space: nowrap;
    padding: 10px 22px;
    color: #010606;
    font-size: 16px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;


}    
.Resume:hover {
    transition: all 0.2s ease-in-out;
    background: #fff;
    color: #010606;
  }

