@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Permanent Marker', cursive;
}

.Resume{
    border-radius: 50px;
    background: #fcba03;
    white-space: nowrap;
    padding: 10px 22px;
    color: #010606;
    font-size: 16px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;


}    
.Resume:hover {
    transition: all 0.2s ease-in-out;
    background: #fff;
    color: #010606;
  }


.Button{
    border-radius: 50px;
    background: #fcba03;
    white-space: nowrap;
    padding: 10px 22px;
    color: #010606;
    font-size: 16px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;


}    
.Button:hover {
    transition: all 0.2s ease-in-out;
    background: #fff;
    color: #010606;
  }

