@use '../abstracts/variables' as *;



*{
  
 
  }
  .btn { 
      background-color: #fff;
      padding:10px 20px;
      display:inline-block;
      border-radius:3px;
      font-size:16px;
      color:#333;
      border-radius: 10px;
}
  body {
    background-color: $mainGrey;
    }
    .container_main{
    position: absolute;  
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .cont_error {
    position: absolute;
    width: 100%;
    height: 300px;
    top: 50%;
    margin-top:-200px;
  }
  
  .cont_error > h1  {
  font-family: $font;
  font-weight: 400;
  font-size:150px;
  color:$mainWhite;
  position: relative;
  left:-100%;
  transition: all 0.5s;
  }
  
  
  .cont_error > p  {
  font-family: $font;
  font-weight: 300;
  font-size:24px;
  letter-spacing: 5px;
  color:$primary;
  position: relative;
  left:100%;
  transition: all 0.5s;
  transition-delay: 0.5s;
  -webkit-transition: all 0.5s;
   -webkit-transition-delay: 0.5s;
  }


//   div 1
  .cont-1 {
  position:absolute;
  width:600px;
  height: 120%;
  top:500px;
  right: -440px;
  background-color: $primary;
  box-shadow: 0px 0px  60px  20px  $mainHoverDark;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  }
  .cont_error_active > .cont_error > h1 {
    left:0%;
  }
  .cont_error_active > .cont_error > p {
    left:0%;
  }
  .cont_error_active > .cont-1 {
    transform: rotate(20deg);
    right:-170px;
    animation-name: animation_error_1;
    animation-duration: 4s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate;
   }
   @keyframes animation_error_1 {
    from {
      -webkit-transform: rotate(20deg);
    transform: rotate(20deg);
    }
    to {  -webkit-transform: rotate(25deg);
    transform: rotate(25deg);
    }
  }




// div 2
.cont-2 {
    position:absolute;
    width:900px;
    height: 220%;
    top:650px;
    right: -440px;
    background-color: $primary;
    box-shadow: 0px 0px  60px  20px  $mainHoverDark;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    -transition: all 0.5s;
  }
  @keyframes animation_error_2 {
    from { -webkit-transform: rotate(-15deg); 
    transform: rotate(-15deg);
    }
    to { -webkit-transform: rotate(-20deg);
    transform: rotate(-20deg);
    }
  }
.cont_error_active > .cont-2 {
    right:-370px;
    animation-name: animation_error_2;
    animation-duration: 4s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    transform: rotate(-20deg);    
  }


  

//   div 3
.cont-3 {
    position:absolute;
    width:900px;
    height: 120%;
    top:750px;
    right: -440px;
    background-color: $primary;
    box-shadow: 0px 0px  60px  20px  $mainHoverDark;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
  }
  @keyframes animation_error_3 {
    from { -webkit-transform: rotate(-15deg); 
    transform: rotate(-45deg);
    }
    to { -webkit-transform: rotate(-20deg);
    transform: rotate(-60deg);
    }
  }
  .cont_error_active > .cont-3{
     transform: rotate(20deg);
     left:-20px;
     animation-name: animation_error_3;
     animation-duration: 8s;
     animation-timing-function: linear;
     animation-iteration-count: infinite;
      animation-direction: alternate;
   }
 